.navBar {
  height: 80px;
  left: 0;
  width: 100%;
  z-index: 100;
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  border: 1px solid #ebebeb;
}

.navContainer {
  align-items: center;
  display: flex;
  flex-direction: row;
  width: 84.078%;
  justify-content: space-between;
}

.navLogo {
  height: 32px;
}

.userInteraction {
  width: 150px;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
}

.link {
  text-decoration: none;
  color: rgb(54, 54, 54);
  font-weight: 600;
  /* margin: 15px; */
}
